import { HypothesisKanban } from "@/components/hypotheses/hypothesis-kanban";
import { HypothesisDetailPanel } from "@/components/hypotheses/hypothesis-detail";
import { HypothesisMatrix } from "@/components/hypotheses/hypothesis-matrix";
import { HypothesisOperationalInbox } from "@/components/hypotheses/hypothesis-ops";
import { HypothesisSnapshot } from "@/components/hypotheses/hypothesis-snapshot";
import { HypothesisActivityTimeline } from "@/components/hypotheses/hypothesis-activity";
import { fetchHypothesisDashboard } from "@/lib/hypothesis-api";

export default async function HypothesesPage() {
  const dashboard = await fetchHypothesisDashboard();

  return (
    <div className="flex flex-col gap-8">
      <HypothesisSnapshot highlights={dashboard.highlights} />

      <HypothesisKanban stages={dashboard.stages} />

      <HypothesisDetailPanel detail={dashboard.focusHypothesis} />

      <HypothesisMatrix stages={dashboard.stages} />

      <HypothesisOperationalInbox tasks={dashboard.tasks} />

      <HypothesisActivityTimeline events={dashboard.activity} />
    </div>
  );
}
