import { HypothesisDashboard, mockHypothesisDashboard } from "./hypothesis-data";

const DEFAULT_BASE_URL = "http://localhost:8001";

function resolveBaseUrl() {
  return process.env.NEXT_PUBLIC_HYPOTHESIS_API || process.env.HYPOTHESIS_API_URL || DEFAULT_BASE_URL;
}

export async function fetchHypothesisDashboard(): Promise<HypothesisDashboard> {
  const baseUrl = resolveBaseUrl();

  try {
    const response = await fetch(`${baseUrl}/hypotheses/dashboard`, {
      next: { revalidate: 30 },
      headers: { "Content-Type": "application/json" }
    });

    if (!response.ok) {
      throw new Error(`Failed to load hypothesis dashboard: ${response.status}`);
    }

    return (await response.json()) as HypothesisDashboard;
  } catch (error) {
    console.warn("[HypothesisDashboard] Falling back to mock data", error);
    return mockHypothesisDashboard;
  }
}
